import React, { Component } from 'react'
import style from './index.module.css'
import { connect } from 'dva'
import Grid from './grid'
import routerConfig from '../../config/router.config'
let views = [];
function transforRouterConfig(arr){
    arr.forEach(item=>{
        let {id:view_id,text:view_authority_text} = item;
        views.push({view_id,view_authority_text});
        if(item.children){
            transforRouterConfig(item.children);
        }
    })
}
transforRouterConfig(routerConfig);
let mapState = store => {
    return {...store.addUser,...store.showUser }
}

@connect(mapState)
class AddUser extends Component {
    componentDidMount(){
        this.props.dispatch({ type: 'showUser/requestUser' });
        this.props.dispatch({ type: 'showUser/requestIdentity' });
        this.props.dispatch({ type: 'showUser/requestApiAuthority' });
        this.props.dispatch({ type: 'showUser/requestIdentityApi' });
        this.props.dispatch({type:'showUser/requestViewAuthority'});
        this.props.dispatch({type:'showUser/changeState',views})
    }
    render() {
        let {viewDatas} = this.props;
        return <div className={style.grid}>
            {
                viewDatas.map((item,key)=>{
                    return <Grid index={key} key={key} viewData={item} {...this.props} />
                })
            }
            
        </div>
    }
}
export default AddUser
