import React,{Component} from 'react'
import { Input, Select, Radio,Button } from 'antd'
import style from './index.module.css'
import {connect} from 'dva'
const Option = Select.Option;
const RadioGroup = Radio.Group;
const RadioButton = Radio.Button;
let mapState = store => {
    window.store = store;
    return {}
}
@connect(mapState)
class Grid extends Component{
    render(){
        let {viewData,index} = this.props;
        let {
            titles,
            titleIndex,
            views,
            writedData,
            btns,
            resourceName,
            idKey,
            status
        } = viewData
        return <div className={style.wrapper}>
            <div className={style.input}>
                <RadioGroup 
                    value={titleIndex} 
                    onChange={ev=>this.props.dispatch({
                        type:'addUser/changeTitleIndex',index,titleIndex:ev.target.value})}
                >
                    {
                        titles.map((item,key)=>{
                            return <RadioButton value={key} key={key}>
                                {item}
                            </RadioButton>
                        })
                    }
                </RadioGroup>
            </div>
            <React.Fragment>
                {
                    views.map((item,key)=>{
                        if(item.titleIndex.indexOf(titleIndex) === -1){
                            return null;
                        }
                        if(item.type === 'select'){
                            return <div key={key} className={style.input}>
                                <Select 
                                    style={{minWidth:150}} 
                                    placeholder={item.placeholder}
                                    onChange={value=>this.props.dispatch({type:'addUser/changeWritedVal',index,key:item.idKey,value,resource:this.props[item.resourceName]})}
                                    value={writedData[item.idKey]}
                                >
                                    {
                                        this.props[item.resourceName].map((value,key)=>{
                                            return <Option 
                                                key={key} 
                                                value={value[item.idKey]}
                                            >{value[item.textKey]}</Option>;
                                        })
                                    }
                                </Select>
                            </div>
                        }
                        if(item.type === 'input'){
                            return <div key={key} className={style.input}>
                                <Input
                                    placeholder={item.placeholder} 
                                    onChange={ev=>this.props.dispatch({type:'addUser/changeWritedVal',index,key:item.idKey,value:ev.target.value})}
                                    value={writedData[item.idKey]}
                                />
                            </div>
                        }
                        return null;
                    })
                }
            </React.Fragment>
            <div className={style.input}>
                <Button 
                    type="primary"
                    style={{marginRight:10}}
                    loading={status === 3 ? true : false}
                    onClick={()=>this.props.dispatch({
                        type:'addUser/postData',
                        index,
                        titleIndex,
                        idKey,
                        postName:btns[titleIndex],writedData,
                        resourceName,
                        resource:this.props[resourceName]
                    })}
                ></Button>
                <Button
                    onClick={()=>this.props.dispatch({type:'addUser/resetWritedVal',index})}
                ></Button>
            </div>
        </div>
    }
}

export default Grid;